<template>
    <div class="wrapper">
      <div class="title">
        <span>v3.0</span>
        <el-button size="mini" plain type="info" icon="el-icon-setting" circle @click="isSettingShow=true"></el-button>
        <el-button size="mini" plain type="success" icon="el-icon-edit" circle @click="isDialogShow=true"></el-button></div>
      <div v-if="ans.length > 0" class="count">
        {{ rightCount }}/{{ totalCount }}({{ rightRate }}%)
      </div>
      <div v-if="ans.length > 0" class="ope">
        <el-input v-model="input" placeholder="" :disabled="isWin" @keyup.enter.native="guess()"></el-input>
        <el-button type="primary" class="btn" :disabled="input.length !== 1" @click="guess()"></el-button>
        <el-button type="warning" class="btn" :disabled="isWin" @click="giveUp()"></el-button>
      </div>
      <div v-if="ans.length === 0 || isWin" class="aiWrapper">
        <el-button type="success" :disabled="isAIThinking" @click="ai()">
          <span v-if="isAIThinking"><i class="el-icon-loading"></i>10</span>
          <span v-else>AI</span>
        </el-button>
      </div>
      <div class="content">
        <div v-for="(p, index) in ans" :key="index" class="paragraph">
          <div v-for="(c, index2) in p" :key="index2" class="block" @click="clickBlock(index, index2)"
          :class="{
            white: c.status === 0,
            black: c.status === 1,
            green: c.status === 2,
            out: c.status === 3,
            last: c.character === lastC && c.status === 2
          }">
            <span v-if="c.status !== 1">{{ c.character }}</span>
          </div>
        </div>
      </div>
      <div v-if="ans.length > 0" class="history">
        <div class="top"></div>
        <div class="bot">
          <div v-for="(h, index) in his" :key="index" class="block" @click="clickHis(index)"
          :class="{
            right: h.status,
            wrong: !h.status,
            last: h.character === lastC && h.status
          }">{{ h.character }}</div>
        </div>
      </div>

      <el-dialog title="" center :visible.sync="isDialogShow" width="75%" @close="isDialogShow=false">
        <el-input type="textarea" :rows="10" placeholder="" v-model="textarea"></el-input>
        <span slot="footer" class="">
          <el-button type="success" :disabled="isAIThinking2" @click="aiCustomize()">
            <span v-if="isAIThinking2"><i class="el-icon-loading"></i></span>
            <span v-else>AI</span>
          </el-button>
          <el-button type="primary" :disabled="textarea.length===0 || isAIThinking2" @click="customize()"></el-button>
        </span>
      </el-dialog>

      <el-dialog title="" center :visible.sync="isSettingShow" width="75%" @close="isSettingShow=false">
        <div class="settingRow">
          <span></span>
          <el-switch v-model="settings.isQuick" @change="saveSettings"></el-switch>
        </div>
        <el-input type="textarea" :disabled="!settings.isQuick" :rows="3" placeholder="" v-model="settings.quickChars" @input="saveSettings"></el-input>
        <span slot="footer" class="">
          <el-button type="" @click="isSettingShow=false"></el-button>
        </span>
      </el-dialog>
    </div>
  </template>

  <style scoped lang="scss">
    .wrapper{
      max-width: 800px;
      margin: 0 auto;
      padding: 0 20px;
      .title{
        margin-top: 20px;
        margin-bottom: 20px;
        font-size: 24px;
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        align-items: center;
        button{
          font-size: 16px;
          margin-left: 6px;
        }
      }
      .count{
        text-align: center;
      }
      .ope{
        margin-top: 20px;
        background-color: white;
        padding: 10px 0;
        display: flex;
        justify-content: center;
        align-items: center;
        position: sticky;
        top: 0;
        .btn{
          width: 100px;
          margin-left: 10px;
        }
      }
      .aiWrapper{
        margin: 20px 0;
        display: flex;
        justify-content: center;
      }
      .content{
        margin-top: 10px;
        .paragraph{
          margin-bottom: 10px;
          display: flex;
          flex-flow: row wrap;
          .block{
            width: 24px;
            height: 24px;
            line-height: 24px;
            font-size: 20px;
            text-align: center;
            margin: 0 5px 5px 0;
            border: 1px solid rgba(255, 255, 255, 0);
            cursor: default;
          }
          .white{

          }
          .black{
            background-color: #333;
          }
          .green{
            background-color: rgb(51,103,209);
            color: white;
          }
          .out{
            border-color: #ccc;
          }
          .last{
            background-color: #EE82EE;
            font-weight: bold;
          }
        }
      }
      .history{
        margin: 10px 0;
        border-top: 1px solid #ccc;
        .top{
          color: #666;
          font-size: 16px;
          margin: 10px 0;
        }
        .bot{
          color: white;
          display: flex;
          flex-flow: row wrap;
          .block{
            width: 24px;
            height: 24px;
            line-height: 24px;
            font-size: 20px;
            text-align: center;
            margin: 0 5px 5px 0;
            cursor: default;
          }
          .right{background-color: rgb(51,103,209);}
          .wrong{background-color: rgb(184, 184, 184);}
          .last{
            background-color: #EE82EE;
            font-weight: bold;
          }
        }
      }

      .settingRow{
        margin-bottom: 20px;
        display: flex;
        flex-flow: row nowrap;
        justify-content: left;
        align-items: center;
        span{
          margin-right: 5px;
        }
      }
    }
  </style>

  <script>
    const { Configuration, OpenAIApi } = require("openai");
    import {Base64} from "js-base64"

    export default {
      name: "guessBK",
      data(){
        return{
          punctuationMarks: `,.\\/:;!?\`~'"()<>[]+-*_%`,
          rightCount: 0,
          totalCount: 0,
          ans: [],
          his: [],
          input: "",
          isWin: false,
          isDialogShow: false,
          isSettingShow: false,
          textarea: "",
          isAIThinking: false,
          isAIThinking2: false,
          lastC: "",
          settings: {
            isQuick: false,
            quickChars: "",
          },
        }
      },
      computed: {
        rightRate(){
          if(this.rightCount === 0 && this.totalCount === 0) return 0;
          return Math.floor(this.rightCount / this.totalCount * 100 * 100) / 100;
        }
      },
      mounted(){
        //
        if(localStorage.getItem("isQuick")){
          this.settings.isQuick = localStorage.getItem("isQuick") === "1";
        } else {
          localStorage.setItem("isQuick", 0);
        }
        if(localStorage.getItem("quickChars")){
          this.settings.quickChars = localStorage.getItem("quickChars");
        } else {
          localStorage.setItem("quickChars", "");
        }

        if(this.$route.query.k){
          let code = this.$route.query.k;
          let decode = Base64.decode(decodeURIComponent(code));
          this.generate(decode);
        }
      },
      methods: {
        getRandomBKCategory(){
          let arr = [
            //xxx
            "", "", "", "",//
            "", "", "", //
            "", "", "", "", //
            "", "", "", //
            "", "", "", "", //
            "", "", "", //
            "", //
            "", "", "", //
            "", //
            "", "", //
            "", //
            "", "Bup", "", //
          ];
          return arr[Math.floor(Math.random() * arr.length)];
        },
        async generate(article){
          this.rightCount = 0;
          this.totalCount = 0;
          this.ans = [];
          this.his = [];
          this.input = "";
          this.isWin = false;
          this.lastC = "";
          let paragraphs = article.split("##").map(e => e.trim());
          for(let p of paragraphs){
            let temp = [];
            for(let c of p){
              temp.push({
                character: c,
                status: this.punctuationMarks.includes(c) ? 0 : 1 //0 1 2 3
              });
            }
            this.ans.push(temp);
          }
          if(this.settings.isQuick){
            for(let c of this.settings.quickChars){
              this.input = c;
              await new Promise(resolve => setTimeout(resolve, 200));
              this.guess(false);
            }
          }
        },
        async ai(){
          const configuration = new Configuration({
            basePath: 'https://api.deepseek.com',
            apiKey: Base64.decode("c2stOTVjNmYxMzQ3YWYxNDEzMjk2ODQ1NTc5NGM4NDAyMmY="),
          });
          delete configuration.baseOptions.headers['User-Agent'];
          const openai = new OpenAIApi(configuration);
          this.isAIThinking = true;
          try {
            const completion = await openai.createChatCompletion({
              messages: [{ role: "user", content: "" + this.getRandomBKCategory() + "23200250" }],
              model: "deepseek-chat",
              temperature: 2
            });
            let rawAns = completion.data.choices[0].message.content;
            rawAns = rawAns.replaceAll("**", "").replaceAll("\n\n", "\n").replaceAll("\n", "##");
            this.generate(rawAns);
            this.isAIThinking = false;
          } catch (error) {
            this.$message.error('');
            this.isAIThinking = false;
          }
        },
        guess(showMessage = true){
          if(this.input.length !== 1) {
            if(showMessage){
              this.$message.warning({
                message: "",
                duration: 1000
              });
            }
            return;
          }
          if(this.punctuationMarks.includes(this.input)) {
            if(showMessage) {
              this.$message.warning({
                message: "",
                duration: 1000
              });
            }
            return;
          }

          this.lastC = this.input;

          //
          if(this.his.some(h => h.character === this.input)){
            if(showMessage){
              this.$message({
                message: "",
                duration: 1000
              });
            }
            this.input = "";
            return;
          }

          //
          let flag = false;
          for(let p of this.ans){
            for(let c of p){
              if(c.character === this.input){
                c.status = 2;
                flag = true;
              }
            }
          }

          if(flag){
            this.rightCount++;
          } else {
            if(showMessage){
              this.$message.error({
                message: "",
                duration: 1000
              });
            }
          }
          this.totalCount++;

          //
          this.his.push({
            character: this.input,
            status: flag
          });

          //
          this.input = "";

          //
          this.judgeWin();
        },
        judgeWin(){
          if(this.ans[0].every(c => c.status === 2)){
            for(let p of this.ans){
              for(let c of p){
                if(c.status === 1){
                  c.status = 3;
                }
              }
            }
            this.isWin = true;
            this.lastC = "";
            this.$message.success({
              message: "",
              duration: 1000
            });
          }
        },
        async aiCustomize(){
          if(this.textarea.length === 0){
            this.$message.warning({
              message: "",
              duration: 1000
            });
            return;
          }
          if(this.textarea.includes("\n")){
            this.$message.warning({
              message: "",
              duration: 1000
            });
            return;
          }
          const configuration = new Configuration({
            basePath: 'https://api.deepseek.com',
            apiKey: Base64.decode("c2stOTVjNmYxMzQ3YWYxNDEzMjk2ODQ1NTc5NGM4NDAyMmY="),
          });
          delete configuration.baseOptions.headers['User-Agent'];
          const openai = new OpenAIApi(configuration);
          this.isAIThinking2 = true;
          try {
            const completion = await openai.createChatCompletion({
              messages: [{ role: "user", content: "" + this.textarea + "23200250" }],
              model: "deepseek-chat",
              temperature: 2
            });
            let rawAns = completion.data.choices[0].message.content;
            rawAns = rawAns.replaceAll("**", "").replaceAll("\n\n", "\n");
            this.textarea = this.textarea + "\n" + rawAns;
            this.isAIThinking2 = false;
          } catch (error) {
            this.$message.error('');
            this.isAIThinking2 = false;
          }
        },
        customize(){
          if(!this.textarea.includes("\n")){
            this.$message.warning({
              message: "",
              duration: 1000
            });
            return;
          }
          let temp = this.textarea;
          temp = temp.replaceAll(/\n/g, "##");
          let fullurl = window.location.href;
          if(fullurl.includes("?")){
            fullurl = fullurl.substring(0, fullurl.indexOf("?"));
          }
          let url = fullurl + "?k=" + encodeURIComponent(Base64.encode(temp));
          navigator.clipboard.writeText(url);
          console.log(url);
          this.$message.success({
              message: "",
              duration: 1000
            });
        },
        giveUp(){
          if (confirm("")) {
            for(let p of this.ans){
              for(let c of p){
                if(c.status === 1){
                  c.status = 3;
                }
              }
            }
            this.isWin = true;
            this.lastC = "";
          }
        },
        clickBlock(index, index2){
          if(this.ans[index][index2].status === 2 && !this.isWin){
            this.lastC = this.ans[index][index2].character;
          }
        },
        clickHis(index){
          if(this.his[index].status && !this.isWin){
            this.lastC = this.his[index].character;
          }
        },
        saveSettings(){
          localStorage.setItem("isQuick", this.settings.isQuick ? 1 : 0);
          localStorage.setItem("quickChars", this.settings.quickChars);
        }
      }
    }
  </script>



